.Project {
  width: 100vw;
  height: 300vh;
  background: #fff;
  display: flex;
  position: relative;
  top: 0; left: 0;
  z-index: 1;
  .inner {
    width: 75%;
    height: 100vh;
    margin: 0 auto;
    .project01 {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 100vh;
      .text {
        display: flex;
        flex-direction: column;
        font-family: 'Pretendard-Regular';
        font-weight: 700;
        font-style: normal;
        font-size: 26px;
        h3 {
          font-size: 3vh;
        }
        h1 {
          font-size: 4vh;
          padding: 30px 0;
        }
        h4 {
          padding-bottom: 30px;
          line-height: 1.5;
          font-weight: normal;
          font-size: 2.5vh;
        }
        ul {
          display: flex;
          li {
            color: #aaa;
            font-weight: normal;
            font-size: 2.2vh;
            padding-right: 10px;
          }
        }
        .btnBox {
          display: flex;
          .btn {
            font-size: 2.3vh;
            margin-top: 30px;
            color: #fff;
            border: 1px solid #000;
            box-sizing: border-box;
            width: 140px;
            height: 50px;
            border-radius: 30px;
            line-height: 50px;
            text-align: center;
            transition: .5s;
            margin-right: 15px;
            background: #000;
            &:hover {
              background: #6BB3F2;
              border: 1px solid #6BB3F2;
              color: #fff;
            }
          }
          .btn2 {
            font-size: 2.3vh;
            margin-top: 30px;
            color: #000;
            border: 1px solid #000;
            box-sizing: border-box;
            width: 140px;
