.Experience {
  width: 100vw;
  height: 100vh;
  background: #6BB3F2;
  position: relative;
  top: 0; left: 0;
  z-index: 1;
  .inner {
    width: 75%;
    height: 100vh;
    margin: 0 auto;
    .content01 {
      width: 35%;
      padding-top: 75px;
      h1 {
        color: #fff;
        font-size: 6vh;
        font-family: 'GapyeongWave';
        font-variation-settings: 'opsz' 1;
      }
    }
    .imgBox {
      font-family: 'Pretendard-Regular';
      margin-top: 7%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      border: 1px solid #fff;
      position: relative;
      width: 300px;
      height: 500px;
      top: 0; left: 0;
      border-radius: 0 0 13px 13px;
      .img {
        height: 200px;
      }
      .description {
        color: #fff;
        .name {
          padding: 15px 0;
          font-size: 2.2vh;
          margin-top: 10px;
          span {
            font-size: 1.5vh;
            padding-left: 10px;
            color: #ccc;
          }
        }
        .buttom {
          display: flex;
          align-items: center;
          .icon {
            display: flex;
            flex-direction: column;
            align-items: start;
          }
          .text {
            font-size: 1.5vh;
            display: flex;
            flex-direction: column;
            line-height: 1.8;
            .tel {
              display: flex;
              align-items: center;
            }
            .mail {
              display: flex;
              align-items: center;
            }
            .addr {
              display: flex;
              align-items: center;
            }
          }
        }
      }
    }
    .imgBox::before {
      content:'';
      width: 300px;
      height: 30px;
      background: #fff;
      position: absolute;
      bottom: 0; left: 0;
      border-radius: 0 0 10px 10px;
    }
  }
  .menu {
    width: 65%;
    height: 100%;
    background: #fff;
    position: absolute;
    bottom: 0; right: 0;
    z-index: 2;
    display: flex;
    .prize {
      flex: calc(100%/3);
      height: 100%;
      background: #85C8F2;
      transition: .5s;
      display: flex;
      flex-direction: column;
      box-shadow: inset 3px 0 3px #5990D9;
      &:hover {
        flex: 300%;
        background: #6BB3F2;
      }
      .majorBox {
        flex: 30%;
        display: flex;
        padding-top: 75px;
        h1 {
          padding-left: 75px;
          color: #fff;
          font-size: 2.5vh;
          font-family: 'Pretendard-Regular';
          flex: 25%;
        }
        .text01 {
          color: #fff;
          flex: 75%;
          h2 {
            font-size: 2vh;
            padding-bottom: 20px;
          }
          h2:nth-child(3) {
            padding-top: 60px;
          }
          p {
            font-size: 1.7vh;
          }
        }
      }
      .majorBox2 {
        flex: 30%;
        display: flex;
        h1 {
          padding-left: 75px;
          color: #fff;
          font-size: 2.5vh;
          font-family: 'Pretendard-Regular';
          flex: 25%;
        }
        .text01 {
          color: #fff;
          flex: 75%;
          h2 {
            font-size: 2vh;
            padding-bottom: 20px;
          }
          h2:nth-child(3) {
            padding-top: 60px;
          }
          p {
            font-size: 1.7vh;
          }
        }
      }
    }
    .award {
      flex: calc(100%/3);
      height: 100%;
      background: #91D7F2;
      transition: .5s;
      display: flex;
      flex-direction: column;
      box-shadow: inset 3px 0 3px #6BB3F2;
      &:hover {
        flex: 300%;
        background: #6BB3F2;
      }
      .majorBox {
        flex: 30%;
        display: flex;
        padding-top: 75px;
        h1 {
          padding-left: 75px;
          color: #fff;
          font-size: 2.5vh;
          font-family: 'Pretendard-Regular';
          flex: 15%;
        }
        .text01 {
          color: #fff;
          flex: 85%;
          h2 {
            font-size: 2vh;
            padding-bottom: 20px;
          }
          h2:nth-child(3) {
            padding-top: 60px;
          }
          h2:nth-child(5) {
            padding-top: 60px;
          }
          p {
            font-size: 1.7vh;
          }
        }
      }
      .majorBox2 {
        flex: 30%;
        display: flex;
        h1 {
          padding-left: 75px;
          color: #fff;
          font-size: 2.5vh;
          font-family: 'Pretendard-Regular';
          flex: 15%;
        }
        .text01 {
          color: #fff;
          flex: 85%;
          h2 {
            font-size: 2vh;
            padding-bottom: 20px;
          }
          h2:nth-child(3) {
            padding-top: 60px;
          }
          h2:nth-child(5) {
            padding-top: 60px;
          }
          .icon {
            display: flex;
            p {
              margin-left: 15px;
              align-items: center;
              font-size: 1.7vh;
              display: flex;
              flex-direction: column;
              justify-content: center;
              span {
                padding-top: 15px;
                font-size: 12px;
              }
            }
          }
        }
      }
    }
    .experience {
      flex: calc(100%/3);
      height: 100%;
      background: #A0EAF2;
      transition: .5s;
      box-shadow: inset 3px 0 3px #85C8F2;
      &:hover {
        flex: 300%;
        background: #6BB3F2;
      }
      .majorBox {
        display: flex;
        padding-top: 75px;
        h1 {
          padding-left: 75px;
          color: #fff;
          font-size: 2.5vh;
          font-family: 'Pretendard-Regular';
          flex: 28%;
        }
        .text01 {
          color: #fff;
          flex: 70%;
          h2 {
            font-size: 2vh;
            padding-bottom: 20px;
          }
          h2:nth-child(3) {
            padding-top: 60px;
          }
          h2:nth-child(5) {
            padding-top: 60px;
          }
          h2:nth-child(7) {
            padding-top: 60px;
          }
          h2:nth-child(9) {
            padding-top: 60px;
          }
          p {
            font-size: 1.7vh;
            line-height: 1.3;
          }
        }
      }
    }
  }
}
.prize .majorBox .text01 {
  display: none;
  font-family: 'Pretendard-Regular';
}
.prize:hover .majorBox .text01 {
  animation: opacity .5s ease-in forwards;
  display: block;
}
.prize .majorBox2 .text01 {
  display: none;
  font-family: 'Pretendard-Regular';
}
.prize:hover .majorBox2 .text01 {
  animation: opacity .5s ease-in forwards;
  display: block;
}
.experience .majorBox .text01 {
  display: none;
  font-family: 'Pretendard-Regular';
}
.experience:hover .majorBox .text01 {
  animation: opacity .5s ease-in forwards;
  display: block;
}
.award .majorBox2 .text01 {
  opacity: 0;
  font-family: 'Pretendard-Regular';
}
.award:hover .majorBox2 .text01 {
  animation: opacity .5s ease-in forwards;
}
.award .majorBox .text01 {
  opacity: 0;
  font-family: 'Pretendard-Regular';
}
.award:hover .majorBox .text01 {
  animation: opacity .5s ease-in forwards;
}
@keyframes opacity {
  0% {opacity: 0;}
  100% {opacity: 1;}
}
