.Contact {
  width: 100vw;
  height: 100vh;
  background: #6BB3F2;
  position: relative;
  top: 0; left: 0;
  z-index: 1;
  .inner {
    width: 75%;
    height: 100vh;
    margin: 0 auto;
    .content {
      padding-top: 75px;
      h1 {
        color: #fff;
        font-family: 'GapyeongWave';
        font-variation-settings: 'opsz' 1;
        text-align: center;
        font-size: 6vh;
        opacity: 1;
        margin-top: 50px;
      }
      .text {
        width: 100%;
        font-family: 'Pretendard-Regular';
        display: flex;
        align-items: center;
        justify-content: space-between;
        ul {
          width: 45%;
          color: #fff;
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 100%;
          position: absolute;
          top: -20%; left: 50%;
          transform: translate(-50%,20%);
          margin: 0 auto;
          li {
            position: relative;
            top: 0; left: 0;
            font-family: "salo-variable", sans-serif;
            border: 1px solid #fff;
            width: 250px;
            height: 250px;
            margin-right: 50px;
            background: #6BB3F2;
            transition: .5s;
            div {
              position: absolute;
              top: 50%; left: 50%;
              transform: translate(-50%,-50%);
              display: flex;
              flex-direction: column;
              align-items: center;
              p {
                font-size: 3vh;
              }
            }
            &:hover {
              background: #fff;
              color: #6BB3F2;
            }
          }
        }
      }
      .my {
        margin-top: 30px;
        position: absolute;
        left: 50%; top: 73%;
        transform: translate(-50%,-73%);
        h1 {
          margin-top: 10px;
          font-size: 30px;
          color: #fff;
          opacity: 1;
        }
      }
      .copy {
        font-family: 'Pretendard-Regular';
        text-align: center;
        color: #fff;
        position: absolute;
        bottom: 130px;
        left: 50%;
        transform: translate(-50%);
      }
    }
  }
}
a {
  color: inherit;
  text-decoration: none;
}