.Home {
  width: 100vw;
  height: 100vh;
  background: #6BB3F2;
  overflow: hidden;
  .home {
    position: relative;
    top: 0; left: 0;
    .inner {
      max-width: 1450px;
      margin: 0 auto;
      height: 100vh;
      .title {
        text-align: center;
        color: #fff;
        font-size: 6rem;
        font-family: 'GapyeongWave';
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        h3 {
          position: absolute;
          top: 39%; left: 50%;
          transform: translate(-50%,-39%);
          font-size: 4vh;
          font-weight: normal;
          letter-spacing: 7px;
        }
        h1 {
          letter-spacing: 65px;
          position: absolute;
          top: 50%; left: 50%;
          transform: translate(-50%,-50%);
        }
        h2 {
          letter-spacing: 20px;
          font-family: 'Pretendard-Regular';
          font-weight: lighter;
        }
        h2 {
          position: absolute;
          top: 63%; left: 50%;
          transform: translate(-50%,-63%);
          font-family: 'Pretendard-Regular';
          font-size: 3.8vh;
        }
      }
      .major {
        color: #fff;
        position: fixed;
        bottom: -50px; left: 0;
        ul.majorBox {
          font-family: "salo-variable",sans-serif;
          font-variation-settings: 'opsz' 1;
          font-weight: normal;
          font-size: 47vh;
          display: flex;
          flex-direction: column;
          justify-content: center;
          height: 100vh;
          opacity: 0.1;
          position: relative;
          top: 0; left: 0;
          cursor: context-menu;
          li {
            
          }
        }
      }
      .scroll {
        font-family: 'Pretendard-Regular';
        position: absolute;
        left: 50%; bottom: -65px;
        transform: translate(-50%);
        z-index: 2;
        color: #fff;
        border: 1px solid #fff;
        border-radius: 75px;
        width: 130px;
        height: 130px;
        line-height: 85px;
        h1 {
          font-size: 2.5vh;
          text-align: center;
          animation: scroll 2s linear infinite;
        }
      }
    }
    
  }
}
@keyframes scroll {
  0% {
    transform: translate(-0,-15px);
    opacity: .1;
  }
  100% {
    transform: translate(-0,-0);
    opacity: 1;
  }
}