.App {
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'GapyeongWave';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2312-1@1.1/GapyeongWave.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

ul.menu {
  font-family: 'Pretendard-Regular';
  font-size: 1.8vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
  position: fixed;
  top: 50%; right: 75px;
  z-index: 5;
  transform: translate(0,-50%);
  li {
    width: 100px;
    height: 100px;
    border-radius: 50px;
    cursor: pointer;
    line-height: 100px;
    align-items: center;
    transition: .5s;
    h1 {
      text-align: center;
    }
  }
  li:not(:last-child) {
    margin-bottom: 15px;
  }
}